import React from 'react'
import { Text, Modal } from '@robustswap-libs/uikit'
import Divider from '@material-ui/core/Divider'
import useStyles from '../../../assets/styles'

interface RecentModalProps {
  onDismiss?: () => void
}

const RecentModal: React.FC<RecentModalProps> = ({ onDismiss }) => {
  const classes = useStyles.chart()

  return (
    <Modal title="Recent Transactions" onDismiss={onDismiss}>
      <Divider style={{ backgroundColor: '#3924B5' }} />
      <Text style={{ color: '#A0B9FB', marginTop: 16 }}>No recent transactions</Text>
    </Modal >
  )
}

export default RecentModal