import React, { useEffect, useState } from 'react'
import { Button, Modal, Input, Flex, Text, useModal } from '@robustswap-libs/uikit'
import { fetchWbnbBalance } from 'state/pools/fetchPoolsUser'
import { getFullDisplayBalance } from 'utils/formatBalance'
import BigNumber from 'bignumber.js'
import styled from 'styled-components'
import useTokenBalance from 'hooks/useTokenBalance'
import Divider from '@material-ui/core/Divider'
import ManageModal from './ManageModal'

interface TokenSelectModalProps {
  setCurrency?: (currency: string, balance: string) => void
  onDismiss?: () => void
  tokens: Array<any>
}

interface CoinProps {
  imgUrl: string
  coinName: string
  coinSymbol: string
  coinAddress: string
  onDismiss?: () => void
  setCurrency?: (currency: string, balance: string) => void
}

const Container = styled.div`
  max-height: 335px;
  margin-top: 10px;
  margin-left: -24px;
  margin-right: -24px;
  padding-left: 24px;
  padding-right: 24px;
  height: 100vh;
  overflow-y: scroll;
  background: #151745;
`

const Wrapper = styled.div`
  display: flex;
  padding: 12px 4px;
  justify-content: space-between;
`

const Coin: React.FC<CoinProps> = ({ imgUrl, coinName, coinSymbol, coinAddress, setCurrency, onDismiss }) => {
  const balance = getFullDisplayBalance(useTokenBalance(coinAddress));

  return (
    <Wrapper onClick={() => {
      setCurrency(coinSymbol, balance)
      onDismiss()
    }}>
      <Flex>
        <img src={imgUrl} alt="" style={{ width: 24, height: 24, marginTop: 'auto', marginBottom: 'auto', marginRight: 5 }} />
        <div>
          <Text style={{ fontSize: 16, fontWeight: 600, lineHeight: 1.5 }}>{coinSymbol}</Text>
          <Text style={{ fontSize: 14, fontWeight: 400, lineHeight: 1.5 }}>{coinName}</Text>
        </div>
      </Flex>
      <Text style={{ marginTop: 'auto', marginBottom: 'auto' }}>{balance}</Text>
    </Wrapper>
  )
}

const TokenSelectModal: React.FC<TokenSelectModalProps> = ({ onDismiss, tokens, setCurrency }) => {
  const [filter, setFilter] = useState('')
  const [count, setCount] = useState(30)
  const [onManage] = useModal(
    <ManageModal />,
    true
  )

  return (
    <Modal title="Select a Token" onDismiss={onDismiss}>
      <Divider style={{ backgroundColor: '#3924B5', marginBottom: 10 }} />
      <Input
        id="search"
        name="search-card"
        type="text"
        placeholder="Search name or paste address"
        style={{ height: 55, borderRadius: 15 }}
        value={filter}
        onChange={(e) => setFilter(e.currentTarget.value)}
      />
      <Container>
        {
          tokens.filter(x => { return x.symbol.startsWith(filter) && x.name; }).slice(0, count).map(element => <Coin key={element.symbol} imgUrl={element.logoURI} coinName={element.name} coinSymbol={element.symbol} coinAddress={element.address} onDismiss={onDismiss} setCurrency={setCurrency} />)
        }
      </Container>
      <Button variant="text" style={{ color: 'white', width: '100%', fontSize: 18 }} onClick={onManage}>Manage Tokens</Button>
    </Modal >
  )
}

export default TokenSelectModal
