import React, { useEffect, useState } from 'react'
import { ButtonMenu, ButtonMenuItem, Input, Modal, Text, Toggle } from '@robustswap-libs/uikit'
import Divider from '@material-ui/core/Divider'
import useStyles from '../../../assets/styles'

interface RecentModalProps {
  onDismiss?: () => void
}

const RecentModal: React.FC<RecentModalProps> = ({ onDismiss }) => {
  const [select, setSelect] = useState(0)
  const [apiExtend, setApiExtend] = useState(false)
  const [apiTop, setApiTop] = useState(false)
  const filter = ""
  /*
  useEffect(() => {
    setSelect(1)
  }, [select])
*/
  return (
    <Modal title="Manage" onDismiss={onDismiss}>
      <ButtonMenu activeIndex={select} size="sm" variant="subtle" onClick={val => setSelect(val)}>
        <ButtonMenuItem>Lists</ButtonMenuItem>
        <ButtonMenuItem>Tokens</ButtonMenuItem>
      </ButtonMenu>
      <Input
        id="search"
        name="search-card"
        type="text"
        placeholder={select ? "0x" : "https:// or ipfs:// or ENS name"}
        style={{ height: 40, borderRadius: 8, marginTop: 24, marginBottom: 16 }}
        value={filter}
      />
      {!select ? <>
        <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: 15 }}>
          <div style={{ width: '85%' }}>
            <img src="/images/farms/RBS.png" alt="" style={{ float: 'left', width: 40, height: 40, marginRight: 10 }} />
            <div>
              <Text style={{ fontSize: 16, fontWeight: 600, lineHeight: 1.5 }}>RobustSwap Extended</Text>
              <Text style={{ fontSize: 14, fontWeight: 400, lineHeight: 1.5 }}>234 tokens</Text>
            </div>
          </div>
          <div style={{ marginTop: 'auto', marginBottom: 'auto' }}>
            <Toggle scale="md" checked={apiExtend} onChange={() => setApiExtend(!apiExtend)} />
          </div>
        </div>
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <div style={{ width: '85%' }}>
            <img src="/images/farms/RBS.png" alt="" style={{ float: 'left', width: 40, height: 40, marginRight: 10 }} />
            <div>
              <Text style={{ fontSize: 16, fontWeight: 600, lineHeight: 1.5 }}>RobustSwap Top 100</Text>
              <Text style={{ fontSize: 14, fontWeight: 400, lineHeight: 1.5 }}>100 tokens</Text>
            </div>
          </div>
          <div style={{ marginTop: 'auto', marginBottom: 'auto' }}>
            <Toggle scale="md" checked={apiTop} onChange={() => setApiTop(!apiTop)} />
          </div>
        </div> </> :
        <>
          0 Custom Token
        </>
      }
    </Modal >
  )
}

export default RecentModal