import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useCakeRouter } from 'hooks/useContract'
import usePairs from 'hooks/usePairs'
import { Button, Text, ChevronDownIcon, Input, useModal } from '@robustswap-libs/uikit'
import { TokenAmount, Token } from '@pancakeswap-libs/sdk'
import { fetchWbnbBalance } from 'state/pools/fetchPoolsUser'
import { getFullDisplayBalance } from 'utils/formatBalance'
import axios from 'axios'
import TokenSelectModal from './TokenSelectModal'

export interface TokenValueProps {
  max?: boolean
  prefix?: string
  coin?: string
  amount?: number
  exclude?: string
  setCurToken?: (token: any) => void
  setPrevToken?: (token: any) => void
}

const Wrapper = styled.div`
  padding: 16px 24px 8px 24px;
  width: 100%;
  border-radius: 16px;
  background: #151745;
  margin: 8px 0px;
`

const TokenDiv: React.FC<TokenValueProps> = ({
  max,
  prefix = 'From',
  coin = '',
  amount,
  exclude = '',
  setCurToken,
  setPrevToken
}) => {
  const [tokens, setTokens] = useState([])
  const [balance, setBalance] = useState('Loading')
  let currency = coin
  const [input, setInput] = useState(amount)
  const { account } = useWallet()
  const routerContract = useCakeRouter()
  const [tokenAddress, setTokenAddress] = useState('0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c')
  // const currencyIn = new Token(56, '0xf307910A4c7bbc79691fD374889b36d8531B08e3', 18, 'ANKR', 'Ankr')
  // const currencyOut = new Token(56, '0xDB021b1B247fe2F1fa57e0A87C748Cc1E321F07F', 9, 'AMPL', 'Ampleforth')
  // const Pairs = usePairs(currencyIn, currencyOut)
  // const currencyAmountIn = new TokenAmount(currencyIn, '1000000000000000000');
  // Trade.bestTradeExactIn(Pairs, currencyAmountIn, currencyOut, { maxHops: 3, maxNumResults: 1 })[0] ?? null

  useEffect(() => {
    axios.get('https://tokens.pancakeswap.finance/pancakeswap-extended.json')
      .then(response => {
        let temp = response.data.tokens
        const bnb =
        {
          symbol: "BNB",
          logoURI: "/images/farms/BNB.png",
          name: "BNB",
          decimals: 18,
          address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
          amount: 0
        }
        temp.push(bnb)
        setTokens(temp)
        if (currency === 'BNB' && account) {
          fetchWbnbBalance(account).then(res => {
            console.log("message")
            const bal = getFullDisplayBalance(new BigNumber(res), 18)
            setBalance(Number(bal).toFixed(6))
          })
        }
        else if (currency !== '') {
          const coinAddress = temp.filter(x => { return x.symbol === currency })
          // setBalance(getFullDisplayBalance(useTokenBalance(coinAddress)))
          setTokenAddress(coinAddress[0].address)

        }
      })
  }, [account, currency])



  useEffect(() => {
    setInput(amount)
  }, [amount])

  const getEstimatePrice = async (contract: any, amountIn: number, decimals: number, tokenA: string, tokenB: string) => {
    const bnb =
    {
      symbol: "BNB",
      logoURI: "/images/farms/BNB.png",
      name: "BNB",
      decimals: 18,
      address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
      amount: 0
    }
    const val = await contract.methods.getAmountsOut(
      new BigNumber(amountIn).times(new BigNumber(10).pow(decimals)),
      [tokenA, tokenB]
    ).call()
    return val;
  }
  let url = ""
  if (currency === 'BNB')
    url = "/images/farms/" + coin + ".png"
  else if (currency === '')
    url = ""
  else
    url = tokens.length !== 0 ? tokens.filter(x => { return x.symbol === currency })[0].logoURI : ''

  const setCurrency = (newCurrency, newBalance) => {
    currency = newCurrency;
    const temp = tokens.filter(x => { return x.symbol === currency })[0]
    const tokenCur = {
      symbol: currency,
      logoURI: temp ? temp.logoURI : '',
      name: temp ? temp.name : '',
      decimals: currency === "BNB" ? 18 : (temp ? temp.decimals : 0),
      address: currency === "BNB" ? "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c" : (temp ? temp.address : ''),
      amount: Number(input)
    }
    setCurToken(tokenCur)
    setBalance(newBalance)
  }

  const [onSelectToken] = useModal(
    <TokenSelectModal tokens={tokens.filter(x => { return x.symbol !== exclude })} setCurrency={setCurrency} />,
    true
  )


  const onChangeCurrentAmount = e => {
    setInput(e.target.value)

    if (e.target.value !== 0) {
      const temp = tokens.filter(x => { return x.symbol === currency })[0]
      const tokenCur = {
        symbol: currency,
        logoURI: temp ? temp.logoURI : '',
        name: temp ? temp.name : '',
        decimals: currency === "BNB" ? 18 : (temp ? temp.decimals : 0),
        address: currency === "BNB" ? "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c" : (temp ? temp.address : ''),
        amount: Number(e.target.value)
      }
      setCurToken(tokenCur)
      let estimatePrice = 0
      if (exclude !== '') {
        const temp1 = tokens.filter(x => { return x.symbol === exclude })[0]
        getEstimatePrice(routerContract, Number(e.target.value), temp1.decimals, tokenCur.address, temp1.address).then(res => {
          estimatePrice = Number(getFullDisplayBalance(new BigNumber(res[res.length - 1])))
          const tokenPrev = {
            symbol: exclude,
            logoURI: temp1 ? temp1.logoURI : '',
            name: temp1 ? temp1.name : '',
            decimals: exclude === "BNB" ? 18 : (temp1 ? temp1.decimals : 0),
            address: temp1 ? temp1.address : '',
            amount: estimatePrice
          }
          setPrevToken(tokenPrev)
        })
      }
    }
  }

  return (
    <Wrapper>
      <Grid container spacing={2}>
        <Grid item sm={6} xs={12} style={{ maxWidth: '50%' }}>
          <Text style={{ fontSize: 12, lineHeight: '14px' }}>{prefix}</Text>
        </Grid>
        <Grid item sm={6} xs={12} style={{ maxWidth: '50%' }}>
          <Text style={{ fontSize: 12, lineHeight: '14px', color: '#FCFCFC', float: 'right', width: '100%', textAlign: 'right' }}>
            Balance: {balance ? getFullDisplayBalance(new BigNumber(balance), 0) : '-'}
          </Text>
        </Grid>
        <Grid item sm={4} xs={12} style={{ display: 'flex', maxWidth: '34%', overflow: 'hidden', fontWeight: 600 }}>
          <Input
            id="search"
            name="search-card"
            type="number"
            style={{ border: 'none', padding: 0, marginTop: 'auto', marginBottom: 'auto', color: 'white', fontSize: 24 }}
            onChange={onChangeCurrentAmount}
            value={input}
          />
        </Grid>
        {max && <Grid item sm={2} xs={12} style={{ display: 'flex', maxWidth: '16%' }}>
          <Button variant="text" onClick={() => setInput(Number(balance))} style={{ padding: 0, color: '#6C60F6', marginTop: 'auto', marginBottom: 'auto' }}>MAX</Button>
        </Grid>}
        <Grid item sm={max ? 6 : 8} xs={12} style={{ textAlign: 'right', maxWidth: max ? '50%' : '66%' }}>
          <Button variant="text" style={{ padding: 4, borderRadius: 8 }} onClick={onSelectToken}>
            {currency && <img src={url} alt="" width="24px" height="24px" style={{ marginRight: 4 }} />}
            <Text style={{ color: '#FCFCFC' }}>
              {currency ? currency.toUpperCase() : 'Select a token'}
            </Text>
            <ChevronDownIcon style={{ marginLeft: 10 }} />
          </Button>
        </Grid>
      </Grid>
    </Wrapper >
  )
}

export default TokenDiv