import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { ButtonMenu, ButtonMenuItem } from '@robustswap-libs/uikit'

export interface IfoTabButtonsProps {
  current: number
}

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 48px;
`

const IfoTabButtons: React.FC<IfoTabButtonsProps> = ({
  current
}) => {
  return (
    <Wrapper>
      <ButtonMenu activeIndex={current === 1 ? 1 : 0} size="sm" variant="subtle">
        <ButtonMenuItem as={Link} to='/exchange'>
          Swap
        </ButtonMenuItem>
        <ButtonMenuItem as={Link} to='/exchange/liquidity'>
          Liquidity
        </ButtonMenuItem>
      </ButtonMenu>
    </Wrapper>
  )
}

export default IfoTabButtons
